@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
*{box-sizing:border-box;padding:0;margin:0}html{font-size:10px}@media (max-width: 767px){html{font-size:8px}}body{font-size:1.6rem;font-family:'Nunito', sans-serif;margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}h1,h2,h3{font-family:'Nunito', sans-serif;font-weight:700}h1{position:relative;margin-bottom:6rem;font-size:4rem;display:inline-block;text-transform:uppercase}h1:after{position:absolute;left:0;bottom:-1rem;content:'';background-color:#E43D40;width:100%;height:8px}h1 .emoji{font-size:3.5rem}p,span{font-family:'Nunito', sans-serif;font-weight:400;letter-spacing:1px;font-size:1.8rem;margin-bottom:1rem}a{color:#E43D40;font-weight:600}.container{max-width:85%;margin-left:auto;margin-right:auto;padding-left:1rem;padding-right:1rem}.grid-wrapper{display:grid}.grid-wrapper.col-1{grid-template-columns:1fr}.grid-wrapper.col-2{grid-template-columns:repeat(1, 1fr)}@media (min-width: 544px){.grid-wrapper.col-2{grid-template-columns:repeat(1, 1fr)}}@media (min-width: 768px){.grid-wrapper.col-2{grid-template-columns:repeat(2, 1fr)}}.grid-wrapper.col-3{grid-template-columns:repeat(1, 1fr)}@media (min-width: 544px){.grid-wrapper.col-3{grid-template-columns:repeat(2, 1fr)}}@media (min-width: 768px){.grid-wrapper.col-3{grid-template-columns:repeat(3, 1fr)}}.grid-wrapper.reverse{direction:rtl}.grid-wrapper.align-center{align-items:center}.grid-wrapper.grid-gap-1{grid-gap:1rem}.grid-wrapper.grid-gap-2{grid-gap:2rem}.grid-wrapper.grid-gap-3{grid-gap:3rem}.grid-wrapper.grid-gap-4{grid-gap:4rem}.my-1{margin-top:1rem;margin-bottom:1rem}.my-2{margin-top:2rem;margin-bottom:2rem}.my-3{margin-top:3rem;margin-bottom:3rem}.my-4{margin-top:4rem;margin-bottom:4rem}.my-5{padding-top:5rem;padding-bottom:5rem}.mb-1{margin-bottom:1rem}.mb-2{margin-bottom:2rem}.mb-3{margin-bottom:3rem}.mb-4{margin-bottom:4rem}.mb-5{padding-bottom:5rem}.py-1{padding-top:1rem;padding-bottom:1rem}.py-2{padding-top:2rem;padding-bottom:2rem}.py-3{padding-top:3rem;padding-bottom:3rem}.py-4{padding-top:4rem;padding-bottom:4rem}.py-5{padding-top:5rem;padding-bottom:5rem}.text-center{text-align:center !important}.img-fluid{max-width:100%}.img-fluid.rounded{border-radius:2.5rem}.bg-grey{background-color:#f9f9f9}

@-webkit-keyframes showSticky{from{transform:translate3d(0, -30px, 0)}to{transform:translate3d(0, 0, 0)}}@keyframes showSticky{from{transform:translate3d(0, -30px, 0)}to{transform:translate3d(0, 0, 0)}}.site-header{position:absolute;top:0px;width:100%;background-color:whitesmoke;border-bottom:1px solid #bebebe;display:flex;align-items:center;padding:2rem 1rem;justify-content:flex-end;transition:all .5s ease-in-out;z-index:9999}.site-header.sticky{position:fixed;-webkit-animation:showSticky 600ms ease backwards;animation:showSticky 600ms ease backwards}.site-header .navigation__list{list-style-type:none;display:inline-flex}.site-header .navigation__list-item{padding-left:2rem;padding-right:2rem}.site-header .navigation__link{text-decoration:none;text-transform:uppercase;transition:all 3s ease-in-out;color:#333;font-family:'Nunito', sans-serif;font-weight:700;font-size:1.8rem}.site-header .navigation__link:after{content:"";display:block;width:0;height:3px;background:#F85C70;transition:width .3s}.site-header .navigation__link:hover:after{width:100%}

.about{margin-top:15rem}

.social-links{list-style-type:none;text-align:center;margin-top:2rem;margin-bottom:2rem}.social-links>li{display:inline;margin-right:2rem}.social-links>li .icon{font-size:4rem;color:#E43D40;transition:all .3s ease-in-out}.social-links>li .icon:hover{color:#000}

.card{position:relative;box-shadow:5px 40px 80px 0px rgba(0,0,0,0.05);border-radius:2.5rem;background:#fff;padding:2rem 3rem;border:0}.card__icon{display:block;font-size:3rem;margin-bottom:1rem}.front-end .card__icon{color:#dc0a68}.back-end .card__icon{color:#6a33fa}.cms .card__icon{color:#E43D40}.frameworks-tools .card__icon{color:#587291}.best-practice .card__icon{color:#0CF574}.business-acumen .card__icon{color:#FFD046}.card__title{position:relative;display:inline-block;font-size:2rem;line-height:1.2;margin-bottom:2rem}.card__title:after{position:absolute;content:'';left:0;bottom:-1rem;width:100%;height:4px}.front-end .card__title:after{background-color:#dc0a68}.back-end .card__title:after{background-color:#6a33fa}.cms .card__title:after{background-color:#E43D40}.frameworks-tools .card__title:after{background-color:#587291}.best-practice .card__title:after{background-color:#0CF574}.business-acumen .card__title:after{background-color:#FFD046}.card__skills{list-style-type:none}.card__skills>li{margin-bottom:.5rem}

.portfolio{--color: #E43D40}.portfolio.australian-institute-architects{--color: #0db14b}.portfolio.firststep{--color: #6a33fa}.portfolio.ie-digital-apt-tm{--color: #a19261}.portfolio.ie-digital-movember{--color: #000}.portfolio .media__image{height:600px;object-fit:cover}.portfolio .content{text-align:left}.portfolio .content__title{display:inline-block;position:relative;text-transform:uppercase;padding-left:2rem;margin-bottom:2rem}.portfolio .content__title:after{position:absolute;content:"";top:0;left:0;background-color:var(--color);width:5px;height:100%}.portfolio .content__company{color:var(--color)}.portfolio .content__info{margin:2rem 0;font-size:1.6rem}.portfolio .content__cta{display:inline-block;border:2px solid var(--color);margin-right:1rem;color:var(--color);margin-bottom:4rem;text-decoration:none;text-transform:uppercase;font-weight:700;padding:1rem 3rem;border-radius:25px;transition:all .3s ease-in-out}.portfolio .content__cta:hover{color:#fff;background-color:var(--color)}.portfolio .content__cta.tm-theme{border:2px solid #ef7700;color:#ef7700}.portfolio .content__cta.tm-theme:hover{background-color:#ef7700;color:#fff}.portfolio .content__tags{list-style-type:none}.portfolio .content__tags>li{display:inline-block;background-color:var(--color);font-size:1.4rem;color:#fff;padding:.5rem 1rem;margin-right:1rem;margin-bottom:1rem}

